    /*- Typograpy and resets -*/
    
    html {
        font-size: 16px;
    }
    
    body {
        direction: rtl;
        font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
        font-size: 100%;
        background-color: rgb(228, 226, 226);
    }
    
    h1,
    h2 {
        margin-top: 0;
    }
    
    h1 {
        margin-bottom: 16px;
        font-size: 36px;
        font-weight: 700;
        color: rgb(83, 13, 80);
    }
    
    h2 {
        margin-bottom: 12px;
        font-size: 24px;
        font-weight: bold;
    }
    
    * {
        font-family: 'Raleway', sans-serif !important;
    }
    
    #appNode {
        color: #000;
        padding: 1rem;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: radial-gradient(circle at 50% 16%, #219eb0 32%, #3f679d 88%);
    }
    
    .text-light {
        font-weight: 100;
    }
    
    .text-normal {
        font-weight: 400;
    }
    
    .text-light: 100;
    .relative {
        position: relative;
    }
    
    .absolute {
        position: absolute;
    }
    
    .right-05 {
        right: 0.5rem;
    }
    
    .bottom-05 {
        bottom: 0.5rem;
    }
    
    .block {
        display: block;
    }
    
    .inline-block {
        display: inline-block;
    }
    
    .margin-auto {
        margin: 0 auto;
    }
    
    .text-center {
        text-align: center;
    }
    
    .text-left {
        text-align: left;
    }
    
    .text-right {
        text-align: right;
    }
    
    .rounded {
        border-radius: 500px;
    }
    
    .btn {
        padding: 0.5rem 1rem;
    }
    
    .btn:hover {
        cursor: pointer;
    }
    
    .btn.btn-success {
        background: #5ccb85;
    }
    
    .btn.btn-success:hover {
        background: darken(#5cb85c, 10%);
    }
    
    .success {
        color: #5ccb85;
    }
    
    .success:hover {
        color: #37ae63;
    }
    
    .btn-none {
        background: transparent;
        border: none;
    }
    
    .hidden {
        display: none;
    }
    /*********** CARD *************/
    
    .box-shadow {
        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
    }
    
    .box-shadow:hover {
        box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -1px rgba(0, 0, 0, 0.2);
    }
    
    .hoverable:hover {
        cursor: pointer;
    }
    
    .card_wrapper {
        padding: 0.5rem;
        display: inline-block;
        vertical-align: top;
        width: 70%;
        margin: 0 auto;
        display: block;
        margin-top: 1%;
        margin-right: 25%;
    }
    
    .card_content {
        background: white !important;
        border-radius: 8px;
        padding: 2rem;
        position: relative;
        height: 200px;
    }
    
    .card_content1 {
        background: white !important;
        border-radius: 8px;
        padding: 2rem;
        position: relative;
        height: 500px;
    }
    
    .card_avatar {
        opacity: 0.5;
        transition: 0.3s all ease-out;
    }
    
    .card_content:hover .card_avatar {
        transform: scale(1.5);
        margin-top: 2rem;
    }
    
    .card_title {
        letter-spacing: 1px;
        transition: 0.3s all ease-out;
        margin: 1rem 0 0 0;
    }
    
    .card_content:hover .card_title {
        margin-top: -20px;
    }
    
    .card_job {
        font-style: italic;
        transition: 0.3s all ease-out;
        margin-top: 0;
    }
    
    .card_content:hover .card_job {
        margin-bottom: 1.8rem;
    }
    
    form {
        padding: 1rem;
        margin-right: auto;
        margin-left: auto;
        max-width: 90rem;
        background-color: #fff;
    }
    
    h1 {
        margin-top: 0;
        margin-bottom: 3.236rem;
        text-align: center;
        font-size: 1.618rem;
    }
    
    .form-group {
        padding: 0;
        border: 0;
    }
    
    .form-group+.form-group {
        margin-top: 1rem;
    }
    
    label {
        display: inline-block;
        margin-bottom: .5rem;
        font-size: .75rem;
        text-transform: uppercase;
        touch-action: manipulation;
    }
    
    input,
    textarea {
        display: block;
        padding: .5rem .75rem;
        width: 100%;
        font-size: 1rem;
        line-height: 1.25;
        color: #55595c;
        background-color: #fff;
        background-image: none;
        background-clip: padding-box;
        border-top: 0;
        border-right: 0;
        border-bottom: 1px solid #eee;
        border-left: 0;
        border-radius: 3px;
        transition: all 0.25s cubic-bezier(0.4, 0, 1, 1);
    }
    
    input:focus,
    textarea:focus {
        outline: 0;
        border-bottom-color: #ffab00;
    }
    
    textarea {
        resize: vertical;
    }
    
    .btn {
        display: inline-block;
        padding: .75rem 1rem;
        margin-top: 1.618rem;
        font-weight: 400;
        text-align: center;
        text-transform: uppercase;
        color: #fff;
        vertical-align: middle;
        white-space: nowrap;
        background-color: #ffab00;
        border: 1px solid transparent;
        box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.07);
        cursor: pointer;
        -webkit-user-select: none;
            -ms-user-select: none;
                user-select: none;
        transition: all 0.25s cubic-bezier(0.4, 0, 1, 1);
    }
    
    .btn:focus,
    .btn:hover {
        background-color: #ffc142;
        box-shadow: 0 18px 35px rgba(50, 50, 93, 0.1), 0 8px 15px rgba(0, 0, 0, 0.07);
    }
    
    .btn:focus {
        outline: 0;
    }
